<template>
  <div>
    <h4>Личная информация</h4>
    <div>
      <ul class="cab_list">
        <li>
          <label>Имя:</label> <span> {{ uname }}</span>
        </li>
        <li>
          <label>Фамилия:</label> <span> {{ usname }}</span>
        </li>
        <li>
          <label>Никнейм:</label> <span> {{ username }}</span>
        </li>
        <li>
          <label>Город:</label> <span> {{ cities_key[ucity] }}</span>
        </li>
        <li>
          <label>Телефон:</label> <span> {{ uphone }}</span>
        </li>
        <li>
          <label>Роль:</label> <span> {{ uposition }}</span>
        </li>
      </ul>
    </div>
    <CRow class="clapans" v-if="showList">
      <CCol md="12">
        <CCard>
          <CCardHeader>
            <div class="d-flex aligin-items-center justify-content-between">
              <span>Пользователи</span>
              <div>
                <CButton color="success" @click="addNewValveModal">
                  Добавить нового пользователя
                </CButton>
              </div>
            </div>
          </CCardHeader>
          <CCardBody>
            <CDataTable
              class="m-2"
              hover
              :items="tableItems"
              :fields="tableFields"
              head-color="light"
              column-filter
            >
              <td slot="city" slot-scope="{ item }">
                {{ cities_key[item.city] }}
              </td>
              <td slot="surname" slot-scope="{ item }">
                {{ item.surname }}
              </td>
              <td slot="phone" slot-scope="{ item }">
                {{ item.phone_number }}
              </td>

              <td slot="status" slot-scope="{ item, index }">
                <span
                  class="badge"
                  :class="'badge-' + colorStatus(item.status)"
                  >{{ textStatus(item.status) }}</span
                >
                <br />
                <CSwitch
                  class="mt-1"
                  color="primary"
                  :checked="item.status == 1"
                  :value="item.status"
                  @update:checked="actionStatusChange(item, index)"
                />
              </td>
              <td slot="settings" slot-scope="{ item }">
                <span
                  style="white-space: nowrap; cursor: pointer"
                  class="d-flex align-items-center"
                  @click="showSettingModal(item)"
                  ><CIcon name="cilSettings" class="mr-1" /> Изменить
                </span>
                <span
                  v-if="item.position !== 'main'"
                  style="white-space: nowrap; cursor: pointer; color: red"
                  class="d-flex align-items-center"
                  @click="deleteUser(item)"
                  ><CIcon name="cil-pencil" class="mr-1" /> Удалить
                </span>
              </td>
            </CDataTable>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
    <CModal title="Добавление нового Пользователя" :show.sync="NewValveModal">
      <CCard>
        <CCardBody>
          <CForm v-on:submit.prevent="submitForm()">
            <CSelect
              horizontal
              label="Город"
              :value.sync="setting.city"
              :options="cities"
              required
            />
            <CInput
              type="text"
              v-model="setting.surname"
              label="Фамилия"
              horizontal
              required
            />
            <CInput type="text" v-model="setting.name" label="Имя" horizontal />
            <CInput
              type="text"
              v-model="setting.position"
              label="Позиция"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.phone_number"
              label="Телефон"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.username"
              label="Никнейм"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.passwprd"
              label="Пароль"
              horizontal
              required
            />
            <CButton color="success" type="submit"> Изменить </CButton>
          </CForm>
        </CCardBody>
      </CCard>
      <div slot="footer"></div>
    </CModal>
    <CModal title="Настройки" :ok-disabled="true" :show.sync="SettingsModal">
      <CCard>
        <CCardBody>
          <CForm v-on:submit.prevent="editForm()">
            <CSelect
              horizontal
              label="Город"
              :value.sync="setting.city"
              :options="cities"
              required
            />
            <CInput
              type="text"
              v-model="setting.surname"
              label="Фамилия"
              horizontal
              required
            />
            <CInput type="text" v-model="setting.name" label="Имя" horizontal />
            <CInput
              type="text"
              v-model="setting.position"
              label="Позиция"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.phone_number"
              label="Телефон"
              horizontal
              required
            />
            <CInput
              type="text"
              disabled
              v-model="setting.username"
              label="Никнейм"
              horizontal
              required
            />
            <CInput
              type="text"
              v-model="setting.passwprd"
              label="Пароль"
              horizontal
              required
            />
            <CButton color="success" type="submit"> Изменить </CButton>
          </CForm>
        </CCardBody>
      </CCard>
      <div slot="footer"></div>
    </CModal>
    <div class="alert_block">
      <CAlert color="warning" closeButton v-if="warning">
        {{ alert_message }}
      </CAlert>
      <CAlert color="primary" closeButton v-if="success">
        {{ alert_message }}
      </CAlert>
    </div>
  </div>
</template>

<script>
const qs = require("qs");
const axios = require("axios");
export default {
  name: "Cab",
  data() {
    return {
      alert_message:"",
      cities_name: "",
      cities_key: "",
      cities_arr: "",
      SettingsModal: false,
      NewValveModal: false,
      uname: "",
      usname: "",
      username: "",
      ucity: "",
      uphone: "",
      uposition: "",
      showList: false,
      cities: [
        { value: null, label: "Выберите город" },
        { value: "1", label: "Алма-ата" },
        { value: "2", label: "Нур-Султан" },
        { value: "3", label: "Шымкент" },
        { value: "4", label: "Актобе" },
        { value: "5", label: "Караганда" },
        { value: "6", label: "Тараз" },
        { value: "7", label: "Павлодар" },
        { value: "8", label: "Усть-Каменогорск" },
        { value: "9", label: "Семей" },
        { value: "10", label: "Атырау" },
        { value: "11", label: "Костанай" },
        { value: "12", label: "Кызылорда" },
        { value: "13", label: "Уральск" },
        { value: "14", label: "Петропавловск" },
        { value: "15", label: "Темиртау" },
        { value: "16", label: "Актау" },
        { value: "17", label: "Туркестан" },
        { value: "18", label: "Кокшетау" },
        { value: "19", label: "Талдыкорган" },
        { value: "20", label: "Экибастуз" },
        { value: "21", label: "Рудный" },
      ],
      tableItems: [],
      tableFields: [
        { key: "city", label: "Город " },
        { key: "surname", label: "Фамилия" },
        { key: "name", label: "Имя" },
        { key: "phone", label: "Телефон" },
        { key: "username", label: "Никнейм" },
        { key: "settings", label: "Действия" },
      ],
      setting: {},
    };
  },
  created() {
    let city = auth.cities();
    this.cities_name = city.cities_name;
    this.cities_arr = city.cities;
    this.cities_key = city.cities_key;
    this.getResults();
  },
  methods: {
    getResults() {
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios
        .get("/users/me")
        .then((response) => {
          console.log(response);
          if (response.data[0].position === "main") {
            this.showList = true;
            this.tableItems = response.data[1];
          }
          (this.uname = response.data[0].name),
            (this.usname = response.data[0].surname),
            (this.username = response.data[0].username),
            (this.ucity = response.data[0].city),
            (this.uphone = response.data[0].phone_number),
            (this.uposition = response.data[0].position);
        })
        .catch((error) => {
          if (error.response.status === 401) {
            window.auth.logout();
          }
        });
    },
    addNewValveModal() {
      this.setting = {};
      this.NewValveModal = !this.NewValveModal;
    },
    showSettingModal(item) {
      this.setting = item;
      this.SettingsModal = !this.SettingsModal;
    },
    submitForm() {
      let app = this;
      console.log(this.setting);
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: "/users/new",
        data: qs.stringify({
          username_to_be_created: this.setting.username,
          password: this.setting.passwprd,
          name: this.setting.name,
          surname: this.setting.surname,
          phone_number: this.setting.phone_number,
          position_doljnost: this.setting.position,
          city: this.setting.city,
        }),
      })
        .then(() => {
          this.NewValveModal = false;
          app.warningModal = false;
          app.success = true;
          app.alert_message = "Успешно добавлен";
          app.setting = {};
          setTimeout(function () {
            app.success = false;
          }, 3000);
          this.getResults();
        })
        .catch(function (error) {
          app.warning = true;
          app.alert_message = error.response.data.detail;
          setTimeout(function () {
            app.warning = false;
          }, 3000);
        });
    },
    editForm() {
      let app = this;
      console.log(this.setting);
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: "/users/change",
        data: qs.stringify({
          username_of_user_to_be_changed: this.setting.username,
          password: this.setting.passwprd,
          name: this.setting.name,
          surname: this.setting.surname,
          phone_number: this.setting.phone_number,
          position_doljnost: this.setting.position,
          city: this.setting.city,
        }),
      })
        .then(() => {
          this.SettingsModal = false;
          app.warningModal = false;
          app.success = true;
          app.alert_message = "Успешно изменено";
          app.setting = {};
          setTimeout(function () {
            app.success = false;
          }, 3000);
          this.getResults();
        })
        .catch(function (error) {
          app.warning = true;
          app.alert_message = error.response.data.detail;
          setTimeout(function () {
            app.warning = false;
          }, 3000);
        });
    },
    deleteUser(item) {
      let app = this;
      this.setting = item;
      axios.defaults.headers.common["Authorization"] =
        "Bearer " + window.auth.token;
      axios({
        method: "post",
        url: "/users/delete",
        data: qs.stringify({ to_be_deleted_username: this.setting.username }),
      })
        .then(() => {
          app.warningModal = false;
          app.success = true;
          app.alert_message = "Успешно удален";
          app.setting = {};
          setTimeout(function () {
            app.success = false;
          }, 3000);
          this.getResults()
        })
        .catch(function (error) {
          app.warning = true;
          app.alert_message = error.response.data.detail;
          setTimeout(function () {
            app.warning = false;
          }, 3000);
        });
    },
  },
};
</script>

<style>
.cab_list {
  list-style-type: none;
  padding: 15px;
  background: white;
  border-radius: 5px;
}

.cab_list li {
  margin: 15px 0;
  border-bottom: 1px solid rgb(236, 236, 236);
}

.cab_list li label {
  padding-right: 15px;
  border-right: 1px solid grey;
  font-size: 14px;
  margin-right: 15px;
  font-style: italic;
  color: grey;
  min-width: 80px;
}

.cab_list li span {
  font-size: 16px;
  color: black;
}
</style>